@import url('https://fonts.googleapis.com/css2?family=Josefin+Sans&family=Montserrat:wght@400;800&display=swap');
:root{
    font-size: 16px;
}
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
.App{
    width: 100vw;
    height:100vh;
    background-color: #F7D159;
    flex-direction: column;
    display: flex;
    justify-content: center;
    align-items: center;
}
.App>p{
    padding: 20px;
    font-size: 1.5rem;   
     color: white;font-weight: bold;
 
}

.vert{
    position: fixed;
    display: flex; 
    flex-direction: column;
    top: 50px;
    left:0;
    counter-set: count;
}
.vert span:nth-child(even):before{
    counter-increment: count 100;
     content: counter(count);
     position:relative;
     left:60px;
   top:-10px;
     color: white;
 }
 .vert span{
     display: inline-block;
     /* box-sizing: border-box; */
     width:60px;
     height: 50px;
     border-top: 1px solid white;
 }
 .vert>span:nth-child(odd){
     width: 30px;
 }

 .hori{
    position: fixed;
    display: flex; 
    top: 0;
    left:50px;
    counter-set: count;
}
.hori span:nth-child(even):before{
   counter-increment: count 100;
    content: counter(count);
    position:relative;
    top:60px;
    left:-10px;
    color: white;
}
.hori span{
    display: inline-block;
    /* box-sizing: border-box; */
    width:50px;
    height: 60px;
    border-left: 1px solid white;
}
.hori>span:nth-child(odd){
    height: 30px;
}
.App>p>span{
    font-size:2.5rem;
    

}
.size{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    padding: 10px;
    align-items: center;
    border: 15px solid white;
    background-color: black;
}

.width,.height{
    box-sizing: border-box;
    margin: 2px;
    color: white;
    font-family: 'Josefin Sans', sans-serif;
/* font-family: 'Montserrat', sans-serif; */
font-weight: bold;
    font-size: 7rem;
    
    padding: 15px;
}
.by{
    color: black;
    background-color: white;
    border-radius: 50%;
    width: 50px;
    height: 50px;
    font-size: 40px;
    line-height: 50px;
    text-align: center;

}



h2{
    text-align: center;
    margin-bottom: 10px;
    }
h2>span{
    border-left: 2px solid white;
    display: inline-block;
    border-bottom:2px solid white ;
    width: 20px;
    height: 50px;
}
h2>span:nth-child(odd){
    height: 50%;
}


 h2>span:last-child{

 border-bottom: none;
    
} 

@media (max-width:680px){
    .size{
        flex-direction: column;
    } 
  
    :root{
        font-size: 10px;
    }
}
 @media (max-width:555px){
    .vert , .hori{
        display: none;
    }
 }

